import React, { ReactElement } from "react";
import * as RolesAPI from "../../api/roles";
import { Role, RoleDTO } from "../../api/roles/model.interface";
import { PageHeaderContext } from "../../context/layout";
import AppBtn from "../../components/form/AppBtn";
import AppInput from "../../components/form/AppInput";
import { Grid } from "@material-ui/core";
import withPageLayout from "../../hocs/withPageLayout";
import { withRouter } from "react-router";

const ReportingTypeEdit = (props: any) => {
  const [name, setName] = React.useState("");
  const [code, setCode] = React.useState("");

  const LayoutContext = React.useContext(PageHeaderContext);

  React.useEffect(() => {
    if (props.match.params.id) {
      getRole(props.match.params.id);
    }
  }, [props.match.params.id]);

  const getRole = async (id: number) => {
    try {
      const role: any | Role = await RolesAPI.details(id);
      setName(role.name);
      setCode(role.code);
    } catch (error) {
      console.log({ error });
    }
  };
  const updateRole = async () => {
    props.setShowLoading(true);

    const roleDTO: RoleDTO = {
      name,
      code,
    };
    const updatedRole = await RolesAPI.put(
      parseInt(props.match.params.id),
      roleDTO
    );
    props.setShowLoading(false);
  };

  React.useEffect(() => {
    LayoutContext.setTitle("Edit Role");
  }, []);
  return (
    <>
      <Grid>
        <AppInput
          label="Name"
          onChangeText={(name: string) => {
            setName(name);
          }}
          value={name}
          placeholder="Enter Name"
          prefix={null}
          suffix={null}
        />
        <AppInput
          label="Code"
          onChangeText={(code: string) => setCode(code)}
          value={code}
          placeholder="Enter Code"
          prefix={null}
          suffix={null}
        />
        <AppBtn block title="Update" onClick={() => updateRole()} />
      </Grid>
    </>
  );
};
export default withRouter(withPageLayout(ReportingTypeEdit));
